export interface IconProps {
	kind: IconKind;
	id?: string;
}

/**
 * This component is a thin wrapper around [remixicon](https://remixicon.com/). To see all available icons, visit
 * their website.
 *
 * @param props
 * @returns
 */
export function Icon({ kind, id }: IconProps): JSX.Element {
	return <span className={"Icon ri-" + kind} id={id}></span>;
}

export type PicaIconKind =
	| "file"
	| "url"
	| "text"
	| "unknown"
	| "rename"
	| "delete"
	| "upload"
	| "analyse"
	| "checkout"
	| "results"
	| "next"
	| "back"
	| "failed";

const KIND_MAP: Record<PicaIconKind, IconKind> = {
	file: "file-line",
	url: "link",
	text: "align-left",
	unknown: "file-unknow-line",
	rename: "pencil-line",
	delete: "delete-bin-5-line",
	upload: "upload-cloud-2-line",
	analyse: "search-line",
	checkout: "shopping-cart-2-line",
	results: "list-check",
	next: "arrow-right-s-line",
	back: "arrow-left-s-line",
	failed: "close-circle-fill",
};

export interface PicaIconProps {
	kind: PicaIconKind;
	id?: string;
}

export function PicaIcon({ kind, id }: PicaIconProps): JSX.Element {
	return Icon({ kind: KIND_MAP[kind], id });
}

export interface ItemTypeIconProps {
	type: ItemType | Item.Resource.Type;
	id?: string;
}

export function ItemTypeIcon({ type, id }: ItemTypeIconProps): JSX.Element {
	const kind = typeof type === "string" ? type : toItemType(type);
	return PicaIcon({ kind: kind ?? "unknown", id });
}
